import { submitComponentEvent } from '../client/ophan/ophan';
const buildNewsletterSignUpFormData = (emailAddress, newsletterIdOrList, recaptchaToken) => {
    const pageRef = window.location.origin + window.location.pathname;
    const refViewId = window.guardian.ophan?.pageViewId ?? '';
    const formData = new FormData();
    formData.append('email', emailAddress);
    formData.append('csrfToken', ''); // TO DO - PR on form handlers in frontend/identity to see how/if this is needed
    if (Array.isArray(newsletterIdOrList)) {
        for (const [index, newsletterId] of newsletterIdOrList.entries()) {
            formData.append(`listNames[${index}]`, newsletterId);
        }
    }
    else {
        formData.append('listName', newsletterIdOrList);
    }
    formData.append('ref', pageRef);
    formData.append('refViewId', refViewId);
    formData.append('name', '');
    if (window.guardian.config.switches.emailSignupRecaptcha) {
        formData.append('g-recaptcha-response', recaptchaToken);
    }
    return formData;
};
const postFormData = async (endpoint, formData) => {
    const requestBodyStrings = [];
    for (const [key, value] of formData.entries()) {
        requestBodyStrings.push(`${encodeURIComponent(key)}=${encodeURIComponent(
        // eslint-disable-next-line @typescript-eslint/no-base-to-string -- people can still sign up (apparently)
        value.toString())}`);
    }
    return fetch(endpoint, {
        method: 'POST',
        body: requestBodyStrings.join('&'),
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/x-www-form-urlencoded',
        },
    });
};
export const requestMultipleSignUps = async (emailAddress, newsletterIds, recaptchaToken) => {
    const data = buildNewsletterSignUpFormData(emailAddress, newsletterIds, recaptchaToken);
    return await postFormData(window.guardian.config.page.ajaxUrl + '/email/many', data);
};
export const requestSingleSignUp = async (emailAddress, newsletterId, recaptchaToken) => {
    const data = buildNewsletterSignUpFormData(emailAddress, newsletterId, recaptchaToken);
    return await postFormData(window.guardian.config.page.ajaxUrl + '/email', data);
};
const trackingEventDescriptionToOphanAction = (description) => {
    switch (description) {
        case 'captcha-success':
        case 'captcha-failure':
            return 'ANSWER';
        case 'captcha-error':
            return 'CLOSE';
        case 'captcha-execute':
            return 'EXPAND';
        case 'captcha-not-loaded-when-needed':
            return 'CLOSE';
        case 'form-submit':
            return 'CLICK';
        case 'failure-response':
            return 'RETURN';
        case 'success-response':
            return 'SUBSCRIBE';
    }
};
export const reportTrackingEvent = (componentName, eventDescription, renderingTarget, extraDetails) => {
    const payload = {
        ...extraDetails,
        message: eventDescription,
        timestamp: Date.now(),
    };
    void submitComponentEvent({
        component: {
            componentType: 'NEWSLETTER_SUBSCRIPTION',
            id: componentName,
        },
        action: trackingEventDescriptionToOphanAction(eventDescription),
        value: JSON.stringify(payload),
    }, renderingTarget);
};
